@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
:root {
  --primary: #fe3f40;
  --primary-light: rgba(255, 44, 109, 1);
  --secondary: #03a4ed;
  --black: #1e1e1e;
  --white: #fff;
  --gray: #afafaf;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body,
html {
  font-size: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Poppins", sans-serif !important;
}

::selection {
  background: var(--secondary);
  color: var(--white);
}

.grid:after {
  content: "";
  display: block;
  clear: both;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0px;
  margin-bottom: 0px;
}

ul {
  margin-bottom: 0px;
}

p {
  font-size: 15px;
  line-height: 30px;
  color: #2a2a2a;
}

img {
  width: 100%;
  overflow: hidden;
}

.page-section {
  margin-top: 120px;
}

.section-heading h2 {
  font-size: 30px;
  text-transform: capitalize;
  color: #2a2a2a;
  font-weight: 700;
  letter-spacing: 0.25px;
  position: relative;
  z-index: 2;
  line-height: 44px;
}

.section-heading h2 em {
  font-style: normal;
  color: #03a4ed;
}

.section-heading h2 span {
  color: #fe3f40;
}

.js-preloader {
  position: fixed;
  display: flex;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--white);
  align-items: center;
  justify-content: center;
  opacity: 1;
  visibility: visible;
  z-index: 9999;
  transition: opacity 0.25s ease;
}

.loaded {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.preloader-inner {
  position: relative;
  width: 8.875rem;
  height: 2.5rem;
  background-color: var(--white);
}
.preloader-inner__dot {
  position: absolute;
  width: 1rem;
  height: 1rem;
  top: 0.75rem;
  left: 0.9375rem;
  background-color: var(--primary);
  border-radius: 50%;
  transform: translateX(0);
  animation: dot 2.8s infinite;
}
.preloader-inner__dots {
  transform: translateX(0);
  margin-top: 0.75rem;
  margin-left: 1.9375rem;
  animation: dots 2.8s infinite;
}
.preloader-inner__dots span {
  display: block;
  float: left;
  width: 1rem;
  height: 1rem;
  margin-left: 1rem;
  background-color: var(--primary);
  border-radius: 50%;
}

@keyframes dot {
  50% {
    transform: translateX(6rem);
  }
}
@keyframes dots {
  50% {
    transform: translateX(-1.9375rem);
  }
}
