.our-portfolio .hidden-content {
  background: linear-gradient(105deg, var(--primary) 0%, var(--primary-light) 100%);
  padding: 30px;
  border-radius: 20px;
  text-align: center;
  opacity: 0;
  top: 0;
  visibility: hidden;
  position: absolute;
  z-index: 2;
  transition: all 0.5s;
}
