  width: 20px;
  height: 20px;
  position: absolute;
  content: "";
  left: 50%;
  bottom: -8px;
  margin-left: -5px;
  transform: rotate(45deg);
  background: linear-gradient(105deg, var(--primary) 0%, var(--primary-light) 100%);
  z-index: -1;
}
